.NavBar {
    /* position: sticky; */
    display: flex;
    /* gap: 2em; */
    top: 0;
    height: 5em;
    background-color: #111;
    border-bottom: 1px solid #333;
}

.logo {
    display: flex;
    margin: 1em;
    align-items: center;
    font-size: 2em;
    text-shadow: rgba(255, 255, 255, 1) 0px 0px 7px;
    user-select: none;

}

.logo a {
    text-decoration: none;
    color: white;
}

.menu {
    color: white;
    display: flex;
    justify-content: right;
    flex-grow: 1;
}

.menu ul {
    display: flex;
    gap: 2em;
    padding: 1em;
    margin: 0;
}

.menu li {
    display: block;
}

.menu a {
    display: flex;
    justify-content: center;
    font-size: 1.2em;
    color: white;
    padding-top: 1em;
    text-decoration: none;
}

.menu a:visited {
    color:white;
}

.menu a:hover {
    color: rgb(195, 195, 195);
}

